.slideshow-container {
    max-width: 100%;
    position: relative;
    margin: 0;
    background: grey;
  }

#slide-1{
    width: 50%;
    position: relative;
    margin-left: 25%;
    
}
#main-info-socials {
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    margin-left: 5%;
    margin-right: 5%;
}

#info {
    flex: 1;  /* Left column for info */
    margin-right: 20px;
    padding-left: 20px;
}

#info h1 {
    font-size: 35px;
    margin-bottom: 10px;
}

#info p {
    font-size: 20px;
    line-height: 1.8;
}

#socials {
    flex: 0.5;  /* Right column for socials */
    text-align: center;
}

#socials h2 {
    font-size: 22px;
    margin-bottom: 15px;
}

#socials ul {
    list-style-type: none;
    padding: 0;
}

#socials li {
    margin: 10px 0;
}

#socials img {
    width: 80px;
    height: auto;
}